import Router from "vue-router";
import Vue from 'vue';
import Home from '../view/Home.vue';
import Notifications from '../view/Notifications.vue';
import Schedule from '../view/Schedule.vue';
import Settings from '../view/Settings.vue';
import Vision from '../view/Vision.vue';
import Habit from '../view/Habit.vue';
import Project from '../view/Project.vue';
import Plan from '../view/Plan.vue';
import Activity from '../view/Activity.vue';
import Result from '../view/Result.vue';


Vue.use(Router);

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/', component: Home,
            children: [
                { path: 'vision', component: Vision },
                { path: 'habit', component: Habit },
                {
                    path: 'project', component: Project,
                    children: [
                        { path: 'plan', component: Plan },
                        { path: 'activity', component: Activity },
                        { path: 'result', component: Result }
                    ]
                },
            ]
        },
        { path: '/settings', component: Settings },
        { path: '/notifications', component: Notifications },
        { path: '/schedule', component: Schedule },
    ]
})